import React, { useEffect, useRef, useState } from 'react';

const BGMController = () => {
  const audioRef = useRef(null);
  const [isPlaying, setIsPlaying] = useState(true);
  const [volume, setVolume] = useState(0.3);

  //    BGM  
  useEffect(() => {
    const audio = audioRef.current;
    if (!audio) return;

    //  
    audio.volume = volume;
    audio.loop = true;

    //   
    const playAudio = () => {
      audio.play().catch(err => {
        console.log('   (  ):', err);
        //     
        const handleUserInteraction = () => {
          audio.play();
          document.removeEventListener('click', handleUserInteraction);
        };
        document.addEventListener('click', handleUserInteraction);
      });
    };

    playAudio();

    // 
    return () => {
      audio.pause();
    };
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  //  
  useEffect(() => {
    if (audioRef.current) {
      audioRef.current.volume = volume;
    }
  }, [volume]);

  // / 
  const togglePlayPause = () => {
    const audio = audioRef.current;
    if (!audio) return;

    if (isPlaying) {
      audio.pause();
      setIsPlaying(false);
    } else {
      audio.play();
      setIsPlaying(true);
    }
  };

  return (
    <>
      {/*    */}
      <audio
        ref={audioRef}
        src="/audio/bgm.wav"
        loop
        preload="auto"
      />

      {/* BGM  UI */}
      <div className="fixed bottom-6 left-6 bg-white bg-opacity-90 rounded-xl shadow-lg p-4 flex items-center gap-4 z-40 hover:bg-opacity-100 transition-all">
        {/* /  */}
        <button
          onClick={togglePlayPause}
          className="text-2xl hover:scale-110 transition-transform"
          title={isPlaying ? '' : ''}
        >
          {isPlaying ? '' : ''}
        </button>

        {/*   */}
        <div className="flex items-center gap-2">
          <span className="text-sm text-gray-600"></span>
          <input
            type="range"
            min="0"
            max="1"
            step="0.1"
            value={volume}
            onChange={(e) => setVolume(parseFloat(e.target.value))}
            className="w-24 h-2 bg-gray-300 rounded-lg appearance-none cursor-pointer accent-blue-500"
            title=" "
          />
          <span className="text-xs text-gray-600 w-8 text-right">
            {Math.round(volume * 100)}%
          </span>
        </div>
      </div>
    </>
  );
};

export default BGMController;
